import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/checklist";

class ChecklistService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    let url = `${DEFAULT_PATH}/search?${search}&start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getProductInfo = (uid: string) => {
    let url = `${DEFAULT_PATH}/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  save = (obj: any) => {
    let url = `${DEFAULT_PATH}/manage`;
    return this.makePostRequestWithAuth(url, obj);
  };

  update = (uid: string | undefined, obj: any) => {
    let url = `${DEFAULT_PATH}/manage/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  };
  
  findById = (id: string) => {
    let url = `${DEFAULT_PATH}/uid/${id}`;
    return this.makeGetRequestWithAuth(url);
  };
  activate= (uid: string) =>{
    let url = `${DEFAULT_PATH}/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  }
  Inactivate= (uid: string) =>{
    let url = `${DEFAULT_PATH}/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  }

  modelToAPIParam = () => {};
}

export default new ChecklistService();
